.component-process {
  user-select: none;
  transition: height 500ms ease;
  transition: width 500ms ease;
}

.no-mouse-events {
  pointer-events: none;
}

.component-process g {
  transition: transform 200ms ease;
}

.component-process-card {
  border-color: #ced4d9;
  border-width: 1px;
  border-left-width: 3px;
  border-radius: 3px;
  border-style: solid;
  margin: 5px;
  box-sizing: border-box;
  width: 190px;
  height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
}

.component-process-card:hover {
  border-color: #338fdd;
  box-shadow: 0 0 2px #338fddb3;
  transition: box-shadow 300ms;
}

.component-process-add-btn {
  background-color: #c4c6cf;
  color: #fff;
  height: 20px;
  width: 20px;
  line-height: 19px;
  border-radius: 100%;
  text-align: center;
  cursor: pointer;
  transition: background-color .3s;
}

.component-process-add-btn:hover {
  background-color: #01c1de;
  transition: background-color .3s;
}

.component-process-card-remove {
  display: none;
}

.component-process-card-hover-show-remove:hover .component-process-card-remove {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #ff0000bf;
  color: #fff;
  user-select: none;
  border-radius: 3px;
}
.component-process-available-target {
  background-color: #2df7c4;
}
.component-process-not-available {
  background-color: #f3f5f6;
}

.component-process-background-grid {
  background: #f3f5f6;
  background-image:
    repeating-linear-gradient(90deg, transparent, transparent 19px, #e3e8eb 19px, #e3e8eb 20px),
    repeating-linear-gradient(transparent, transparent 19px, #e3e8eb 19px, #e3e8eb 20px);
}
